<template>
  <v-app id="inspire">
    <nav-drawer :menus="menus" :drawer="drawer" v-model="drawer"/>
    
    <!-- <app-bar v-model="drawer" /> -->

    <v-app-bar
        app
        dark
        class="white primary--text"
    >
        <v-app-bar-nav-icon class="primary--text" @click.stop="drawer = !drawer" />

        <v-toolbar-title justify="center" @mouseover="hoverTitle" @mouseleave="leaveTitle">
            <router-link to="/" class="d-flex flex-nowrap" style="text-decoration: none">
            <div class="d-none d-sm-flex" >보라매&nbsp;&nbsp;</div><v-icon class="primary--text">{{titleIcon}}</v-icon><div> 센트럴바움</div>
            </router-link>
        </v-toolbar-title>
        <v-spacer />
        <v-btn outlined class="primary--text" @mouseover="hoverAccount" @mouseleave="leaveAccount" to="/interest">
            <v-icon class="primary--text">{{accountIcon}}</v-icon><span class="d-none d-sm-flex pl-2">관심고객 등록</span>
        </v-btn>
        <v-btn outlined class="ml-2 primary--text" href="tel:02-849-9015">
            <v-icon class="primary--text">mdi-face-agent</v-icon><span class="d-none d-sm-flex pl-2">02-849-9015</span>
        </v-btn>
    </v-app-bar>

    <v-content>
      <v-bottom-navigation
        :value="activeBtn"
        horizontal
        v-show="!$vuetify.breakpoint.smAndDown"
      >
        <v-menu v-for="(menu,index) in menus" :key="index" open-on-hover offset-y transition="slide-y-transition">
          <template v-slot:activator="{ on }">
              <v-btn v-on="on" class="subtitle-1">
                {{menu.title}}
              </v-btn>
          </template>
          <v-list>
            <v-list-item
              v-for="(submenu, index) in menu.submenus"
              :key="index"
              :to="submenu.path"
            >
              <v-list-item-title class="subtitle-2">{{ submenu.title }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-bottom-navigation>
    </v-content>

  <template v-if="$route.meta && $route.meta.fullLayout">
    <v-container fluid class="pa-0">
      <div class="col-md-12 col-sm-12 col-xs-12 pa-0">
        <router-view/>
      </div>
    </v-container>
  </template>
  <template v-else>
    <div>
      <base-header :title="$route.meta.title" :mainImage="require('../assets/images/bg.jpg')" />
      <v-container fluid>
        <div class="col-md-12 col-sm-12 col-xs-12">
          <router-view/>
        </div>
      </v-container>
    </div>
  </template>

    <base-footer />
  </v-app>
</template>


<script>
import BaseHeader from '@/components/layout/BaseHeader';
import NavDrawer from '@/components/layout/NavDrawer';
import AppBar from '@/components/layout/AppBar';
import BaseFooter from '@/components/layout/BaseFooter';
    export default {
        data () {
            return {
              drawer: false,
              group: null,
              titleIcon: "mdi-cube-outline",
            accountIcon: "mdi-account-plus-outline",
                activeBtn: 1,
                menus: [
                  {
                    title: "사업안내",
                    icon: "mdi-information",
                    model: false,
                    submenus: [
                      {
                        title: "사업개요",
                        path: "/overview",
                        icon: "mdi-information"
                      },
                      {
                        title: "입지환경",
                        path: "/environment",
                        icon: "mdi-map-marker"
                      },
                      {
                        title: "모집공고",
                        path: "/gonggo",
                        icon: "mdi-file-cloud-outline"
                      }
                    ]
                  },
                  {
                    title: "프리미엄",
                    icon: "mdi-google-circles-extended",
                    model: false,
                    submenus: [
                      {
                        title: "입지 프리미엄",
                        path: "/premium1",
                        icon: "mdi-google-circles-extended"
                      },
                      {
                        title: "설계 프리미엄",
                        path: "/premium2",
                        icon: "mdi-globe-model"
                      }
                    ]
                  },
                  {
                    title: "단지안내",
                    icon: "mdi-scatter-plot-outline",
                    model: false,
                    submenus: [
                      {
                        title: "단지 배치도",
                        path: "/danji",
                        icon: "mdi-scatter-plot-outline"
                      },
                      {
                        title: "커뮤니티",
                        path: "/community",
                        icon: "mdi-nfc-tap"
                      },
                      {
                        title: "조감도",
                        path: "/viewmap",
                        icon: "mdi-view-compact-outline"
                      }
                    ]
                  },
                  {
                    title: "분양안내",
                    icon: "mdi-file-chart",
                    model: false,
                    submenus: [
                      {
                        title: "공급안내",
                        path: "/salesinfo",
                        icon: "mdi-file-chart"
                      }
                    ]
                  },
                  {
                    title: "세대안내",
                    icon: "mdi-floor-plan",
                    model: false,
                    submenus: [
                      {
                        title: "평면정보",
                        path: "/housetype",
                        icon: "mdi-floor-plan"
                      }
                    ]
                  },
                ],
            }
        },
        watch: {
          group () {
            this.drawer = false
          },
        },
        methods: {
          hoverTitle: function(){
            this.titleIcon = 'mdi-cube'
          },
          leaveTitle: function(){
              this.titleIcon = 'mdi-cube-outline'
          },
          hoverAccount: function(){
              this.accountIcon = 'mdi-account-plus'
          },
          leaveAccount: function(){
              this.accountIcon = 'mdi-account-plus-outline'
          },
        },
        components: {
          BaseHeader,
          BaseFooter,
          NavDrawer,
          AppBar
        }
    }
</script>



